#link-style {
	font-weight: bold;
	font-size: 1.0em;
	font-family: 'Roboto Condensed', sans-serif;
	color: rgb(243, 243, 247);
	text-transform: uppercase;
}

#link {
	margin-right: 32%;
}

#link a:hover {
	font-size: 20px;
	color: #ffffff;
	text-decoration:underline white;
	;
}

.projects-grid {
	display: flex;
}

.header-color {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
	background-image: url('./images/header-background.jpg');
	
	
	
}

/* Página HOME */

.landing-grid {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
	text-align: center;
	position: fixed;
	width: 100%;
	height: 100%;
	background-image: url('./images/header-background.jpg');
	
}

.avatar-image {
	margin-top: 100px;
	margin-bottom: 0;
	/* margin: auto;     */
	height: 250px;
}

.cell-img {
	height: 20%;
}

.banner-text {
	background-color: black;
	opacity: .7;
	width: 95%;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: auto;
	border-radius: 10px;
}

.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: rgb(255, 255, 255);
	text-align: center;
}

.banner-text hr {
	border-top: 5px dotted white;
	width: 70%;
	margin: auto;
}

.banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
	text-align: center;
}

.social-links {
	display: flex;
	justify-content: space-evenly;
	width: 50%;
	margin: auto;
}

.social-links i {
	color: white;
	font-size: 3em;
	padding: 8px;
}

/* Página do Curriculo CSS */

.resume-col-dir {
	background: #27221F;
	color: white;
	padding: 2em;
}

/* Página de CONTATO */

.contact-body {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 2em;
}

.contact-grid {
	text-align: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	background: white;
	color: black;
	border: 2px solid #29323c;
	margin-bottom: 20px;
	margin-top: auto;
}

.contact-grid h2 {
	font-family: 'Anton', sans-serif;
}

.contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold;
}

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}

.lista-contato i {
	font-size: 66px;
	padding-right: 5rem;
}

.list-content {
	font-size: 25px;
	font-family: 'Anton', sans-serif;
}

@media only screen and (max-width: 376px) {
	.banner-text h1 {
		font-size: 40px;
	}
}

@media only screen and (max-width: 415px) {
	.cell-img {
		height: 50%;
	}
	.landing-grid {
		position: relative;
	}
	/* CONTATO */
	.contact-body {
		position: relative;
		height: auto;
	}
	.contact-grid {
		margin-top: 100px;
		width: 80%;
		height: 80%;
	}
	.lista-contato i {
		font-size: 20px;
		padding-right: 5rem;
	}
	.list-content {
		font-size: 10px;
		font-family: 'Anton', sans-serif;
	}
}